.search-bar {
  position: relative;
  width: 200px;
  transition: width 0.5s ease;
  
}
.search-bar input {
  width: 100%;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  transition: box-shadow 0.5s ease;
}
.search-bar input:focus {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  outline: none;
}
.search-bar button {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  background: transparent;
  border: none;
  padding: 0 10px;
  cursor: pointer;
}
.search-bar button:focus {
  outline: none;
}


